const headers = () => {
  const authHeader = () => {
    const user = JSON.parse(localStorage.getItem("user"));
    if (user && user.token) {
      return `Bearer ${user.token}`;
    } else {
      return {};
    }
  };

  return {
    "Content-type": "application/json",
    Authorization: authHeader(),
  };
};

export default headers;
